:host {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  .header {
    z-index: 2;
  }

  .footer {
    z-index: 2;
  }

  .outlet {
    z-index: 1;
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
    background-image: url('/assets/images/backgrounds/main.png');
    background-color: #f5f5f5;

    ::ng-deep {
      & > *:not(router-outlet) {
        width: 100%;
      }
    }
  }
}
